export default {
	data() {
		return {
			userInfo: {},
			isCanUse: uni.getStorageSync('isCanUse') || false //默认为true
		};
	},
	onLoad() {
		// 默认加载
		this.wxGetCode();
	},
	methods: {
		// 1、获取code
		wxGetCode() {
			let _this = this;
			uni.login({
				provider: 'weixin',
				success: resCode => {
					_this.wxGetUserInfo(resCode.code);
				},
				fail(res) {
					console.log(res);
				}
			});
		},
		// 2、根据code去的微信的用户信息
		wxGetUserInfo(code) {
			let _this = this;
			uni.getUserInfo({
				provider: 'weixin',
				success: res => {
					_this.userInfo = res.userInfo;
					uni.setStorageSync('userInfo', JSON.stringify(res.userInfo));
					console.log(res.userInfo);
					try {
						uni.setStorageSync('isCanUse', true); //记录是否第一次授权  false:表示不是第一次授权
					} catch (e) {}
					this.$request({
						url: '/api/wx/login',
						data: {
							code: code,
							nickName: res.userInfo.nickName,
							avatarUrl: res.userInfo.avatarUrl,
							gender: res.userInfo.gender
						}
					}).then(res => {
						console.log(res);
					});
				},
				fail(res) {
					console.log(res);
				}
			});
		}
	}
};
